
import { PropType, defineComponent } from "vue";
import { IbUser } from "@/views/cib/cib-interface/ib-subscription-edit.interface";
import { currencyFormat } from "@/shared/services/CurrencyFormatService";
import formatInput from "@/shared/components/FormatInput/FormatInput.vue";
import CurrencyInput from '@/shared/components/CurrencyInput/CurrencyInput.vue';
import { CIB20100334_REQ_BODY, CIB20100334_RES } from '@/shared/type/CIB20100334.type'
import { modalController } from "@ionic/vue";
import { APM20200113_RES } from "@/shared/type/APM20200113.type";
import { TransferLimit } from '@/shared/type/CIB20100311.type'
import { screenMode } from "@/shared/type/Approval.type";

const currencyFormatter = new currencyFormat();

export default defineComponent({
  name: "cib201003ss04",
  components: {
    formatInput,
    CurrencyInput
  },
  props: {
    data: {
      type: Object as PropType<IbUser>,
      required: true,
    },
    currentTab: {
      type: String,
      required: true,
    },
  },

  data() {
    const approvalTap = this.$route.query.tab;
    return {
      approvalTap,
      transferLimitData: {
        userID:"",
        customerNo: "",
        limitList: {
          dailyLimitAmount: 0,
          limitCategoryCode:"",
          limitTypeCode:"",
          transactionCurrencyCode:"",
          transactionLimitAmount:0,
          maxDailyAmount: 0,
          maxTransactionAmount: 0,
        },
      },
      tempTransferLimitData: {} as TransferLimit,
      isInquiredInput:false,
      isEditable: false,
      isAllowSubmit: true,
      isExceedTransactionLimitAmount: false,
      isExceedDailyLimiAmount: false,
      exchangeRate: 4000,
      approvalRes: {} as any,
			operationMode: 'normal' as screenMode,
    };
  },

  computed: {
		approvalType(): string {
			return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : '';
		},

    maxTransaction() {
      return currencyFormatter.convertCurrency(this.transferLimitData.limitList.maxTransactionAmount, "USD");
    },

    maxTransactionLimitKhr() {
      return currencyFormatter.convertCurrency(this.transferLimitData.limitList.maxTransactionAmount * this.exchangeRate, "KHR");
    },

    maxDailyLimit() {
      return currencyFormatter.convertCurrency(this.transferLimitData.limitList.maxDailyAmount, "USD");
    },

    maxDailyLimitKhr() {
      return currencyFormatter.convertCurrency(this.transferLimitData.limitList.maxDailyAmount * this.exchangeRate, "KHR");
    },

    remainingLimit() {
      return (this.transferLimitData.limitList.maxTransactionAmount - this.transferLimitData.limitList.transactionLimitAmount);
    },

    remainingLimitKhr() {
      return this.remainingLimit * this.exchangeRate;
    },

    dailyLimitRemaining() {
      return (this.transferLimitData.limitList.maxDailyAmount - this.transferLimitData.limitList.dailyLimitAmount);
    },

    dailyLimitRemainingKhr() {
      return this.dailyLimitRemaining * this.exchangeRate;
    },

    transactionLimitKhr() {
      return this.transferLimitData.limitList.transactionLimitAmount * this.exchangeRate;
    },

    dailyLimitKhr() {
      return this.transferLimitData.limitList.dailyLimitAmount * this.exchangeRate;
    },
  },

  watch: {
    data(customerData: IbUser) {
      if(this.approvalType == ''){
        this.transferLimitData = JSON.parse(JSON.stringify({
        userID: customerData.userInfo.userID,
        customerNo: customerData.userInfo.customerNo,
        limitList: {
          limitCategoryCode: customerData.userTransferLimit[0].limitCategoryCode,
          limitTypeCode: customerData.userTransferLimit[0].limitTypeCode,
          transactionCurrencyCode: customerData.userTransferLimit[0].transactionCurrencyCode,
          maxTransactionAmount: customerData.userTransferLimit[0].maxTransactionAmount,
          transactionLimitAmount: customerData.userTransferLimit[0].transactionLimitAmount,
          maxDailyAmount: customerData.userTransferLimit[0].maxDailyAmount,
          dailyLimitAmount: customerData.userTransferLimit[0].dailyLimitAmount,
        }
      }));
        this.tempTransferLimitData = JSON.parse(JSON.stringify(this.transferLimitData.limitList));
      }else{
        if(this.approvalTap == 'CIB201003SS04'){
          this.$approval.getAuthorizationDetail().then( (authDetail: APM20200113_RES) => {
            let authDetailParsed = JSON.parse(authDetail.body.requestContent);
            if(authDetailParsed.limitList){  // if authDetailParsed.limitList is not empty then let the loop work
              authDetailParsed.limitList.map((item)=>{
                this.transferLimitData.limitList.maxTransactionAmount = item.maxTransactionAmount
                this.transferLimitData.limitList.transactionLimitAmount =   item.transactionLimitAmount 
                this.transferLimitData.limitList.maxDailyAmount = item.maxDailyAmount
                this.transferLimitData.limitList.dailyLimitAmount =   item.dailyLimitAmount  
              })
              this.approvalRes = authDetail.body;
            }
          })
          this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal';
        }
      }
    },

    isEditable(isAllowTabChange) {
      this.$emit("isBeingEdited", isAllowTabChange);
    },

    'transferLimitData.limitList.transactionLimitAmount':function(transactionValue){
      if(transactionValue == ''){
        this.isInquiredInput = true;
      }else if(Number(transactionValue) > this.transferLimitData.limitList.maxTransactionAmount){
        this.isExceedTransactionLimitAmount = true;
      }else if(Number(transactionValue) <= this.transferLimitData.limitList.maxTransactionAmount){
        this.isExceedTransactionLimitAmount = false;
      }
    },

    'transferLimitData.limitList.dailyLimitAmount':function(dailyLimit){
      if(dailyLimit == ''){
        this.isInquiredInput = true;
      }else if(Number(dailyLimit) > this.transferLimitData.limitList.maxDailyAmount){
        this.isExceedDailyLimiAmount = true;
      }else if(Number(dailyLimit) <= this.transferLimitData.limitList.maxDailyAmount){
        this.isExceedDailyLimiAmount = false;
      }
    }
  },

  methods: {
    onClickOperation(btn: string) {
      switch (btn) {
        case "edit":
          this.isEditable = true;
          break;

        case "cancel":
          this.isEditable = false;
          this.transferLimitData.limitList = this.tempTransferLimitData;
          break;

        case "list":
          this.$router.push("/corporate-internet-banking/subscription");
          break;

        case "submit":
          if(!this.isExceedTransactionLimitAmount && !this.isExceedDailyLimiAmount){
            this.$dialog.onOpen({
            content: `${this.$t("message.would_you_like_to_save")}`,
            dialogMode:"Question",
              callback: ()=>{
                this.handleSubmit();
                modalController.dismiss();
              }
            })
          }
          break;
      }
    },

    async handleSubmit() {
      let body: CIB20100334_REQ_BODY = {
        userID: this.transferLimitData.userID,
        customerNo: this.transferLimitData.customerNo,
        limitList: [{
          dailyLimitAmount: Number(this.transferLimitData.limitList.dailyLimitAmount),
          transactionLimitAmount: Number(this.transferLimitData.limitList.transactionLimitAmount),
          transactionCurrencyCode: this.transferLimitData.limitList.transactionCurrencyCode,
          limitCategoryCode: this.transferLimitData.limitList.limitCategoryCode,
          limitTypeCode:this.transferLimitData.limitList.limitTypeCode,
          maxTransactionAmount: this.transferLimitData.limitList.maxTransactionAmount,
          maxDailyAmount : this.transferLimitData.limitList.maxDailyAmount

        }],
      };

      const responseDaTa = await this.$request.getRequest('CIB20100334', body, false, 'CIB201003SS04') as CIB20100334_RES;
      if(this.approvalType){
        this.isEditable = false;
        this.$emit("isReseTButton", false);
      }

      if(responseDaTa.header.result){
        this.isEditable = false;
        this.$emit("isReseTButton", false);
        this.$dialog.onOpen({
          content: this.$t("message.update_success"),
          dialogMode:"Success",
          callback:()=>{
            modalController.dismiss()
          }
        });
      }
    },
  },
});
