
import { currencyCode } from "@/shared/type/common.type";
import { defineComponent, PropType } from "vue";

const BACKSPACE = 8;
const LEFT_ARROW = 37;
const RIGHT_ARROW = 39;
const DELETE = 46;
const ENTER = 13;
const DECIMAL = 190;

export default defineComponent({
    name: "CurrencyInput",
    props: {
        modelValue: { 
            type: String,
            required: true
        },
        currency: {
            type: String as PropType<currencyCode>,
            required: true,
            default: 'USD'
        },
        showCurrency: {
            type: Boolean,
            default: true
        },
        autoConvert: {
            type: String,
            default: 'USD'
        },
        textAlign: {
            type: String as PropType<'left' | 'center' | 'right'>,
            default: 'right'
        }
    },
    emits: ['update:modelValue'],
    data() {
        return {
            displayVal: "0",
            isFocus: false
        }
    },
    mounted() {
        if(this.autoConvert == 'KHR'){
            this.onFormat(`${ Number(this.modelValue) * 4000 }`)
        } else {
            this.onFormat(this.modelValue)
        }
    },
    watch: {
        modelValue(newValue, oldValue) {
            if(this.autoConvert == 'KHR'){
                if (newValue !== oldValue) {
                    if(this.isFocus) {
                        this.onUnformat(newValue * 4000)
                    } else {
                        this.onFormat(newValue * 4000)
                    }
                }
            } else {
                if (newValue !== oldValue) {
                    if(this.isFocus) {
                        this.onUnformat(newValue)
                    } else {
                        this.onFormat(newValue)
                    }
                }
            }
        },
        currency(){
            if(this.autoConvert == 'KHR'){
                if(this.isFocus) {
                    this.onUnformat(Number(this.modelValue) * 4000)
                } else {
                    this.onFormat(Number(this.modelValue) * 4000)
                }
            } else {
                if(this.isFocus) {
                    this.onUnformat(this.modelValue)
                } else {
                    this.onFormat(this.modelValue)
                }
            }
        }
    },
    methods: {
        onUnformat(val) {
            if(!val || val == '0' || val == 0 || val == '0.00') {
                this.displayVal = "0"
                return
            }
            const strVal = val.toString()
            if(typeof strVal == 'string' && strVal.includes(',')) {
                const stringVal = strVal.toString()
                this.displayVal = this.$format.unformatCurrency(stringVal)
            } else {
                this.displayVal = strVal
            }
        },
        onFormat(val) {
            if (!val || val == '0' || val == 0) {
                this.displayVal = this.currency == 'USD' ? "0.00" : '0'
                return
            }
            const strVal = val.toString()
            if(typeof strVal == 'string' && !strVal.includes(',')) {
                const numberVal = parseFloat(strVal) 
                this.displayVal = this.$format.formatCurrency(numberVal, this.currency)
                
            } else {
                this.displayVal = strVal
            }
        },
        onChange(event) {
            const value = event.target.value
            this.$emit('update:modelValue', value);
        },
        onKeyDown(event) {
            const keyEvent = event
            const charCode = (keyEvent.which) ? keyEvent.which : keyEvent.keyCode;

            if (!(/\d/.test(event.key)) // 0-9
                && (charCode !== BACKSPACE)
                && (charCode !== LEFT_ARROW)
                && (charCode !== RIGHT_ARROW)
                && (charCode !== DELETE)
                && (charCode !== ENTER)
                && (charCode !== DECIMAL)
            ){ 
                keyEvent.preventDefault()
            } else {
                if (charCode === ENTER) {
                    this.onBlur()
                } else if(charCode === DECIMAL){
                    if (this.currency == 'KHR') {
                        event.preventDefault()
                    } else {
                        if(typeof this.modelValue === 'string' && this.modelValue.includes('.')) {
                            event.preventDefault()
                        }
                    }
                } else if (/\d/.test(event.key)) {
                    if (typeof this.modelValue === 'string' && this.modelValue.includes('.')) {
                        const split = this.modelValue.split('.')
                        if(split.length > 1) {
                            event.currentTarget.getInputElement().then(el => {
                                const cursorPos = el.selectionEnd
                                if (cursorPos == this.modelValue.length && split[1].length == 2) {
                                    keyEvent.preventDefault()
                                } else if (cursorPos == this.modelValue.length - 1 && split[1].length == 2) {
                                    keyEvent.preventDefault()
                                } else if (cursorPos == this.modelValue.length - 2 && split[1].length == 2) {
                                    keyEvent.preventDefault()
                                }
                            })
                        }
                    }
                }
            }
        },
        onBlur(){
            this.onFormat(this.modelValue)
            this.isFocus = false
        },
        onFocus() {
            this.onUnformat(this.modelValue)
            this.isFocus = true
        }
    }
});
