import type { currencyCode } from "../type/common.type";

export class currencyFormat {
  convertCurrency(amount: number, currencyCode?: currencyCode): string {
    const result = amount.toString().split(".");
    if (currencyCode === "KHR") {
      result[0] = result[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",") + ' KHR' ;
    } else if(currencyCode === "USD"){
      result[0] = result[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",") + ' USD' ;
    } else {
      result[0] = '$' + result[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",") + (result[1] ? "." + result[1] : ".00");
    }
    return result[0];
  }
  unformatCurrency(formatedAmount: string) {
    const format = formatedAmount.split(".");
    const result = format[0].replace(/\D/g, "") + (format[1] != "00" && format[1] ? "." + format[1] : "");
    return result;
  }
}
